
import React, { useEffect } from "react";
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import semihImage from '../images/SemihSay.jpeg';
import Enes from '../images/EnesErkan.jpeg';
import Seyda from '../images/SeydaBurucu.jpeg';
import Kenan from '../images/KenanSofuoğlu.jpeg';
import Tatankalar from '../images/Tatankalar.jpeg';
import Sakarya1 from '../images/SakaryaM.jpg';
import Sakarya2 from '../images/SakaryaMD.jpeg';
import navbarToggle from './navbarToggle';

const defaultIcon = new L.Icon({
  iconUrl: 'https://unpkg.com/leaflet/dist/images/marker-icon.png', // Leaflet'in varsayılan ikonunu buraya ekledik.
  iconSize: [25, 41], // İkonun boyutlarını ayarladık.
  iconAnchor: [12, 41], // İkonun ankraj noktasını belirledik.
  popupAnchor: [1, -34], // Popup yerini ayarladık.
  shadowUrl: 'https://unpkg.com/leaflet/dist/images/marker-shadow.png', // Gölge ekledik.
  shadowSize: [41, 41], // Gölgenin boyutları.
});


const About = () => {
    useEffect(() => {

      navbarToggle();
    // Harita sınırları
    const mapBounds = [
      [40, 29.5], // Güneybatı
      [41.486237, 31.4]  // Kuzeydoğu
    ];

    // Harita başlatma
    const map = L.map('map', {
      center: [40.742623, 30.330722],
      zoom: 12,
      maxBounds: mapBounds,
      maxZoom: 12,
      minZoom: 10,
      zoomControl: true
    });

    // TileLayer ekleme (OpenStreetMap kullanıyoruz)
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      maxZoom: 19,
      attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
    }).addTo(map);

    // Marker'lar ve popup'lar
    const marker1 = L.marker([40.7742490720967, 30.40006071093756], { icon: defaultIcon }).addTo(map);
    const marker2 = L.marker([40.8163732671634, 30.752312246176473], { icon: defaultIcon }).addTo(map);
    const marker3 = L.marker([40.89065383483343, 30.43622463792658], { icon: defaultIcon }).addTo(map);
    const marker4 = L.marker([40.73963636475224, 30.37324144740015], { icon: defaultIcon }).addTo(map);
    const marker5 = L.marker([40.690631386828564, 30.26804198209599], { icon: defaultIcon }).addTo(map);
    const marker6 = L.marker([40.39217154418295, 30.493808711604245], { icon: defaultIcon }).addTo(map);

    // Popup'ları marker'lara ekleme
    marker1.bindPopup('<a href="https://www.sakarya.edu.tr/" target="_blank">Sakarya Müzesi</a>');
    marker2.bindPopup('<a href="https://kulturportali.gov.tr/turkiye/sakarya/gezilecekyer/hendek-bayraktepe" target="_blank">Bayrak Tepe</a>');
    marker3.bindPopup('<a href="https://www.sakarya.edu.tr/" target="_blank">Harmantepe Kalesi</a>');
    marker4.bindPopup('<a href="https://www.sakarya.edu.tr/" target="_blank">Justinaus Köprüsü</a>');
    marker5.bindPopup('<a href="https://www.sakarya.edu.tr/" target="_blank">Cami i Cedid</a>');
    marker6.bindPopup('<a href="https://www.sakarya.edu.tr/" target="_blank">Tarihi Çınar</a>');

    // Cleanup (temizleme) işlemi: Harita kaldırıldığında temizleme yapılır.
    return () => {
      map.remove(); // Harita kaldırıldığında temizlenir
    };
  }, []);

  return (
    <div>

    {/* Menü */}
            <menu className="float__menu">
              <a href="#MyCountry" className="active"><i className="uil uil-estate"></i></a>
              <a href="#History"><i className="uil uil-books"></i></a>
              <a href="#Slider"><i className="uil uil-bag-alt"></i></a>
              <a href="#Map-Sakarya"><i className="uil uil-phone"></i></a>
            </menu>

      {/* İçerikler */}
      <section id="MyCountry" className="MyCountry">
        <div className="section__container">
          <div className="container">
            <h1>Sakarya</h1>
                <p>Sakarya is the most crowded 20th city in Turkey, Sakarya is
                  the most 5th country region of Marmara After İstanbul, Bursa, Kocaeli and Balıkesir.
                  It takes his name Sakarya River which in his region, Sakarya's capital is Adapazarı.
                  Adapazarı won a prize in Danimarka and with that Adapazarı knowns is the Bicycle Country.
                  Sakarya population is 1.098.115 this information depends on 2023 TÜİK's Researching.
                  Sakarya has 16 district (Town) and  668 Neigbourhood. It takes a part on Çatalca Kocaeli district of 
                  Marmara Region. Sakarya Economy is generally depends on Agriculture, They Growing Nut also Corn in these Areas
                  Industry is developing in Capital of Sakarya (Adapazarı). Industriel Establishments increased 
                  these industrialization  Recently Years 
                </p>
          </div>
          <div className="container">
            <h1>Sport Country Sakarya</h1>
                <p>Sakarya is a Sport Country Which to raise BWA and UMB Earth third Banth Champion Semih Saygıner in Billiards
                   Supersport Earth Champion Kenan Sofuoğlu in Motorcycle Race 
                   In Karate Enes Erkan and Şeyda Burucu as a lot of well known Athletes
                  </p>
                  <p>Tatankalar represent our country in Super League American Football and they are too successfull in this sport.
                    Tatankalar which consisting university student, to exhibit Very Succesful Progress Among the University and Super League
                  </p>
          </div>
        </div>
      </section>

      <section id="History" className="Historical-Monument">
        <div className="section__container">
          <div className="container">
            <h1>Sakarya Museum</h1>
            <p>Sakarya Museum is located approximately 3 km from Sakarya city center. Transportation to Sakarya Museum is easily possible by private vehicles, taxis and public transportation.
                Sakarya Museum is a very old building where Gazi Mustafa Kemal Atatürk and his mother stayed. Sakarya Museum contains many archaeological and ethnographic artifacts.
                Sakarya Museum is open to visitors between 08:30 in the morning and 17:30 in the evening during the summer months and between 08:30 and 16:00 in the winter months.       
                There is no entrance fee or entry restriction to enter Sakarya Museum.</p>
          </div>
          <div className="container container__photo">
            <div className="image"><img src={Sakarya1} alt="" /></div>
            <div className="image"><img src={Sakarya2} alt="" /></div>
          </div>
        </div>
      </section>

      <section id="Slider" className="Slider-CountryPhotos">
      <Swiper
        modules={[Pagination, Navigation]}
        spaceBetween={30}
        slidesPerView={1}
        pagination={{ clickable: true }}
        navigation
        loop={true}
        className="mySwiper"
      >
        <SwiperSlide>
          <div className="image__wrapper">
            <img src= {semihImage} alt="Semih Say" />
            <a className="image__link" href="/Semih.html">
              More Information
            </a>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="image__wrapper">
            <img src={Enes} alt="Enes Erkan" />
            <a className="image__link" href="/Enes.html">
              More Information
            </a>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="image__wrapper">
            <img src={Seyda} alt="Enes Erkan" />
            <a className="image__link" href="/Enes.html">
              More Information
            </a>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="image__wrapper">
            <img src={Kenan} alt="Enes Erkan" />
            <a className="image__link" href="/Enes.html">
              More Information
            </a>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="image__wrapper">
            <img src={Tatankalar} alt="Enes Erkan" />
            <a className="image__link" href="/Enes.html">
              More Information
            </a>
          </div>
        </SwiperSlide>
        {/* Diğer slidelar buraya */}
      </Swiper>
    </section>

      <section id="Map-Sakarya" className="Sakarya__Map" style={{ justifyContent: "center", display: "flex", alignItems: "center" }}>
        <div id="map" style={{ height: "90%", zIndex: 2 }}></div>
      </section>
    </div>
  );
};

export default About;


