
import React, { useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation } from "swiper/modules";
import VectorLise from "../images/Lise.jpg"
import Universite from "../images/Universty.jpg"
import Drawing from "../images/Draw.jpeg"
import Chess from "../images/Chess.jpeg"
import Profil from "../images/profil.jpg"

const Home = () => {
    useEffect(() => {
  const apiKey = "485d86be4fc80a56d9a3585850d07a08";
  const apiUrl = "https://api.openweathermap.org/data/2.5/weather?=&units=metric&q=";

  const searchBox = document.querySelector(".search input");
  const searchBtn = document.querySelector(".search button");
  const weatherIcon = document.querySelector(".weather-icon");

  async function checkWeather(city) {
    const response = await fetch(apiUrl + city + `&appid=${apiKey}`);

    if (response.status === 404) {
      document.querySelector(".weather").style.display = "none";
      document.querySelector(".error").style.display = "block";
    } else {
      const data = await response.json();

      document.querySelector(".city").innerHTML = data.name;
      document.querySelector(".temp").innerHTML = Math.round(data.main.temp) + "°C";
      document.querySelector(".humidity").innerHTML = data.main.humidity + "%";
      document.querySelector(".wind").innerHTML = data.wind.speed + "km/h";

      if (data.weather[0].main === "Clouds") {
        weatherIcon.src = "images/clouds.png";
      } else if (data.weather[0].main === "Clear") {
        weatherIcon.src = "images/clear.png";
      } else if (data.weather[0].main === "Rain") {
        weatherIcon.src = "images/rain.png";
      } else if (data.weather[0].main === "Drizzle") {
        weatherIcon.src = "images/drizzle.png";
      } else if (data.weather[0].main === "Mist") {
        weatherIcon.src = "images/mist.png";
      }

      document.querySelector(".weather").style.display = "block";
      document.querySelector(".error").style.display = "none";
    }
  }

  const handleClick = () => {
    checkWeather(searchBox.value);
  };

  searchBtn.addEventListener("click", handleClick);

  return () => {
    searchBtn.removeEventListener("click", handleClick);
  };
}, []);
  return (
    <div>
    <menu class="float__menu">
        <a href="#Home" class="active"><i class="uil uil-estate"></i></a>
        <a href="#CV-Part"><i class="uil uil-books"></i></a>
        <a href="#Swiper"><i class="uil uil-books"></i></a>
        <a href="#Weather"><i class="uil uil-books"></i></a>
    </menu>

    <section className="Home" id="Home">
        <div className="section__container">
            <div className="container">
                
                <h2>Hello It's Me</h2>
                <h1>Emre Koca</h1>
                <h2>And I'am <span>Student</span></h2>
                <p>This is My Portfolio Website</p>
                <div className="social__links">
                    <a href="https://www.facebook.com/"><i className="uil uil-facebook-f"></i></a>
                    <a href="https://www.twitter.com/"><i className="uil uil-twitter"></i></a>
                    <a href="https://www.gmail.com/"><i className="uil uil-envelope"></i></a>
                    <a href=""><i className="uil uil-whatsapp"></i></a>
                </div>
            </div>
            <div className="right__container">
                <div className="image__container">
                    <img src={Profil} alt=""/>
                    <ul>
                        <li>
                            <div className="overlay-slide">Welcome My Website</div>
                        </li>
                        <li><a className="overlay-slide" href="https://www.youtube.com"><i class="uil uil-youtube"></i></a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </section>

    <section className="AboutMe" id="CV-Part">
        <div className="section__container">
            <div className="personal__informations-left">
                <div className="contact__informations">
                    <div className="mini__title">Contact</div>
                    <ul>
                        <li><a href="mailto:Emrekoca005@gmail.com">EmreKoca005@gmail.com</a></li>
                        <li><a href="https://www.facebook.com/">EmreKoca005@hotmail.com</a></li>
                        <li><a href="https://github.com/WhiteMasterIXI/WebTeknolojileri-Sehir-Tanitma">GitHub
                                WhiteMaster</a></li>
                    </ul>
                </div>
                <div className="Programing_Languages">
                    <div className="mini__title">Programing and Markup Languages</div>
                    <ul>
                        <li>
                            <p>C++</p>
                            <div className="progress__bar">
                                <div className="progress__percentages">60%</div>
                            </div>
                        </li>
                        <li>
                            <p>C#</p>
                            <div className="progress__bar">
                                <div className="progress__percentages">70%</div>
                            </div>
                        </li>
                        <li>
                            <p>Html</p>
                            <div className="progress__bar">
                                <div className="progress__percentages">80%</div>
                            </div>
                        </li>
                        <li>
                            <p>Css</p>
                            <div className="progress__bar">
                                <div className="progress__percentages">60%</div>
                            </div>
                        </li>
                        <li>
                            <p>JS</p>
                            <div className="progress__bar">
                                <div className="progress__percentages">40%</div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div className="Languages">
                    <div className="mini__title">Languages</div>
                    <ul>
                        <li>
                            <p>English</p>
                            <div className="circle">
                                <div className="inner">B2</div>
                            </div>
                        </li>
                        <li>
                            <p>Germany</p>
                            <div className="circle">
                                <div className="inner">A2</div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="personal__informations-right">
                <div className="Myintroduce">
                    <h4>Who I am ?</h4>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas suscipit voluptatibus, fugit
                        quam deserunt expedita laudantium eligendi commodi at facere eveniet eos asperiores maxime,
                        neque molestias? Sed consequatur et ipsam!</p>
                </div>
                <div className="Education">
                    <h4>My Education Progress</h4>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Odio quo suscipit ullam aperiam magni
                        ea quas nam quibusdam veritatis praesentium, cupiditate rem incidunt dolorem rerum voluptatibus
                        tenetur laborum eveniet voluptas.</p>
                </div>
                <div className="Hobbys">
                    <h4>My Hobbys</h4>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Culpa eius harum earum?</p>
                </div>
            </div>
        </div>
    </section>

    <section id="Swiper" className="Slider-CountryPhotos">
          <Swiper
            modules={[Pagination, Navigation]}
            spaceBetween={30}
            slidesPerView={1}
            pagination={{ clickable: true }}
            navigation
            loop={true}
            className="mySwiper"
          >
            <SwiperSlide>
              <div className="image__wrapper">
                <img src= {VectorLise} alt="Lise" />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="image__wrapper">
                <img src= {Universite} alt="Lise" />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="image__wrapper">
                <img src= {Drawing} alt="Lise" />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="image__wrapper">
                <img src= {Chess} alt="Lise" />
              </div>
            </SwiperSlide>
            </Swiper>
        </section>

    <section id="Weather" className="Weather-Api">
  <div className="section__container">
    <div className="card">
      <div className="search">
        <input type="text" placeholder="enter city name" spellCheck="false" />
        <button>
          <img src="images/search.png" alt="" />
        </button>
      </div>
      <div className="error">
        <p>Invalid city name</p>
      </div>
      <div className="weather">
        <img src="images/clear.png" className="weather-icon" alt="weather" />
        <h1 className="temp">22°C</h1>
        <h2 className="city">New York</h2>
        <div className="details">
          <div className="col">
            <img src="images/humidity_8691565.png" alt="humidity" />
            <div>
              <p className="humidity">50%</p>
              <p>Humidity</p>
            </div>
          </div>
          <div className="col">
            <img src="images/wind-socket_2402912.png" alt="wind" />
            <div>
              <p className="wind">15 km/h</p>
              <p>Wind Speed</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>

</div>
  );
}

export default Home;
