
import {Link, NavLink} from 'react-router-dom';
import React, { useState, useEffect } from 'react';

const Navbar = () => {
  const [isLoggedIn,setLoggedIn] = useState(false);

useEffect(() => {
  const loggedIn = localStorage.getItem('isLogged');
  setLoggedIn(loggedIn === 'true');
}, []);
  return (
    <div>
      {/* Navbar */}
            <nav>
              <div className="nav__container">
                <div className="nav__logo">
                  <div className="logo__image">
                    <div className="back__side">
                      <div className="face__background">
                        <div className="face">
                          <div className="face__up">
                            <div className="eye"></div>
                            <div className="eye"></div>
                          </div>
                          <div className="face__down"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <Link href="/Home">
                    <h1>Sakarya</h1>
                  </Link>
                </div>
                <ul id="nav__items" className="nav__items">
                  <li className="nav__item"><Link to="/Home">Home</Link></li>
                  <li className="nav__item"><Link to="/about">About</Link></li>
                  <li className="nav__item"><Link to="/contact">Contact</Link></li>
                  {isLoggedIn ? (
              <li className="nav__item"><a href="/webtekno/Logout.php">Exit</a></li>
            ) : (
              <li className="nav__item"><a href="/webtekno/Login.php">Login</a></li>
            )}
                </ul>
                <div className="nav__toggle-buttons">
                  <button id="close_btn" className="close-btn btn"><i className="uil uil-times"></i></button>
                  <button id="open_btn" className="open-btn btn"><i className="uil uil-bars"></i></button>
                </div>
              </div>
            </nav>
    </div>
  );
}

export default Navbar;
