
import Contact from "../images/Contact.jpg"
import React, { useState, useEffect } from 'react';

const Iletisim = () => {
  const [formData, setFormData] = useState({
    name: '',
    surname: '',
    email: '',
    phone: '',
    message: ''
  });

  // React ile denetleme
  const reactKontrol = () => {
    const errors = [];
    if (!formData.name) errors.push("İsim boş olamaz.");
    if (!formData.surname) errors.push("Soyisim boş olamaz.");
    if (!formData.email.includes('@')) errors.push("Geçersiz email.");
    if (!/^\d+$/.test(formData.phone)) errors.push("Telefon sadece rakam olmalı.");
    if (formData.message.length < 10) errors.push("Mesaj çok kısa.");

    if (errors.length > 0) {
      alert("React Kontrol:\n" + errors.join("\n"));
    } else {
      alert("React Kontrol: Başarılı!");
      document.querySelector(".contact__form").submit();
    }
  };

  // JavaScript ile denetleme (input id'lerinden değer alır)
  const jsKontrol = () => {
    const name = document.getElementById("js-name").value.trim();
    const surname = document.getElementById("js-surname").value.trim();
    const email = document.getElementById("js-email").value.trim();
    const phone = document.getElementById("js-phone").value.trim();
    const message = document.getElementById("js-message").value.trim();

    const errors = [];
    if (!name) errors.push("İsim boş olamaz.");
    if (!surname) errors.push("Soyisim boş olamaz.");
    if (!email.includes('@')) errors.push("Geçersiz email.");
    if (!/^\d+$/.test(phone)) errors.push("Telefon sadece rakam olmalı.");
    if (message.length < 10) errors.push("Mesaj çok kısa.");

    if (errors.length > 0) {
      alert("JavaScript Kontrol:\n" + errors.join("\n"));
    } else {
      alert("JavaScript Kontrol: Başarılı!");
      document.querySelector(".contact__form").submit();
    }
  };

  return (
    <>
      <div>
        {/* Menü */}
        <section className="contact">
          <div className="contact-cont contact__container">
            <aside className="contact__aside">
              <div className="aside__image">
                <img src={Contact} alt="Contact" />
              </div>
              <h2>Contact Us</h2>
              <p>metin yaz bizimle iletişimle ilgili</p>
              <ul className="contact__details">
                <li>
                  <i className="uil uil-phone-times"></i>
                  <h5>+2335523456789</h5>
                </li>
                <li>
                  <i className="uil uil-envelope"></i>
                  <h5>suppor@egator.com</h5>
                </li>
                <li>
                  <i className="uil uil-location-point"></i>
                  <h5>Accra, Ghana</h5>
                </li>
              </ul>
              <ul className="contact__socials">
                <li><a href="https://facebook.com"><i className="uil uil-facebook-f"></i></a></li>
                <li><a href="https://instagram.com"><i className="uil uil-instagram-alt"></i></a></li>
                <li><a href="https://twitter.com"><i className="uil uil-twitter"></i></a></li>
                <li><a href="https://linkedin.com"><i className="uil uil-linkedin-alt"></i></a></li>
              </ul>
            </aside>

            <form
              action="https://formspree.io/f/xjkvyzae"
              method="POST"
              className="contact__form"
            >
              <div className="form__name">
                <input
                  id="js-name"
                  type="text"
                  name="name"
                  placeholder="Ad"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                />
                <input
                  id="js-surname"
                  type="text"
                  name="surname"
                  placeholder="Soyad"
                  value={formData.surname}
                  onChange={(e) => setFormData({ ...formData, surname: e.target.value })}
                />
              </div>
              <input
                id="js-email"
                type="email"
                name="email"
                placeholder="E-posta"
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              />
              <input
                id="js-phone"
                type="text"
                name="phone"
                placeholder="Telefon"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
              />
              <textarea id="js-message"
                name="message"
                value={formData.message}
                onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                rows="7"
                placeholder="Message" required></textarea>
              <div className="buttons">
                <button type="button" className="btn btn-primary">Send Message</button>
                <button type="button" onClick={jsKontrol} className="btn btn-primary">Check with JS</button>
                <button type="button" onClick={reactKontrol} className="btn btn-primary">Check with React</button>
              </div>

            </form>
          </div>
        </section>
      </div>
    </>
  );
};

export default Iletisim;
